* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.login {
  display: block;
  margin-left: auto;
  margin-right: auto;
}

input {
  height: 40px;
}

.regForm {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 5% auto;
  padding: 20px 50px;
  background-size: 100%;
  background-repeat:no-repeat;
  background: -webkit-linear-gradient( to left top, #485563, #29323c);
  background: -o-linear-gradient( to left top, #485563, #29323c);
  background: -webkit-gradient( linear, right bottom, left top, from(#485563), to(#29323c));
  background: -webkit-linear-gradient( right bottom, #485563, #29323c);
  background: -o-linear-gradient( right bottom, #485563, #29323c);
  background: linear-gradient( to left top, #485563, #29323c);

  -webkit-box-shadow: 5px 5px 8px #000000;

          box-shadow: 5px 5px 8px #000000;
}



